.films{
	/* max-width: 750px; */
}
.films .toggle-location{
	text-align: center;
	width: 100%;
}
.films .toggle-location h3{
	margin: 0;
}
.films .toggle-location i{
	width: 40px;
	height: 40px;
	border-radius: 20px;
	text-align: center;
	font-size: 20px;
	margin: 0 10px;
	line-height: 40px;
}
.films .toggle-icons{
	margin-top: 20px;
}
.films .toggle-location i.active{
	width: 40px;
	height: 40px;
	border-radius: 20px;
	background-color: #F6BB10;
	text-align: center;
} 

.sort{
	margin-top: 20px;
}
.sort ul{
	display: flex;
	justify-content: flex-start;
}
.sort ul li{
	padding: 5px;
	background-color: #141414;
	border-radius: 10px;
	list-style: none;
	margin-right: 10px;
}

@media screen and (max-width: 540px) { 
	.films h3{
		display: block;
		padding: 0;
		margin: 0 auto 20px;
	}
	.films .toggle-location{
		text-align:  center;
	}
	.sort ul{
		justify-content: center;
		padding-left: 0;
	}
}